<template>
  <div>
    <div class="search-wrap">
      <!-- 右浮动 -->
      <!-- input输入查询框 -->
      <a-select
        showSearch
        :value="content"
        placeholder="股票名称或代码"
        style="width: 160px;margin-left: 11px;float:right;"
        :defaultActiveFirstOption="false"
        :showArrow="false"
        :filterOption="false"
        @search="handleSearch"
        @change="handleChange"
        :notFoundContent="null"
      >
        <a-select-option v-for="d in searchData" :key="d.value">{{d.text}}</a-select-option>
      </a-select>
      <!-- 日期选框 -->
      <a-range-picker
        @change="onDateChange"
        style="width: 230px;margin-left: 11px;float:right;"
        :defaultValue="[moment(startTime, dateFormat), moment(endTime, dateFormat)]"/>
      <div class="menu-date">
        <div @click="handleDateActive(1)" :class="dateActive===1?'menu-li active':'menu-li'">当日</div>
        <div @click="handleDateActive(2)" :class="dateActive===2?'menu-li active':'menu-li'">近3日</div>
        <div @click="handleDateActive(3)" :class="dateActive===3?'menu-li active':'menu-li'">近5日</div>
        <div @click="handleDateActive(4)" :class="dateActive===4?'menu-li active':'menu-li'">近10日</div>
      </div>
      <div class="clear"></div>
    </div>
    <a-table
      :columns="columns"
      :rowKey="record => record.idx"
      :dataSource="tableData"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
      <template slot="name" slot-scope="name, record">
        <a @click="handleClickName(record)">{{record.name}}</a>
      </template>
    </a-table>
  </div>
</template>

<script>
import moment from 'moment'
import {
  getShHoldingShares, // 北向持股
  getShHoldingShareList // 北向持股搜索列表
} from '@/api/index'
const columns = [
  { align: 'center', title: '持股时间', dataIndex: 'date' },
  { align: 'center', title: '代码', dataIndex: 'code' },
  { align: 'center', title: '上市公司', dataIndex: 'name', scopedSlots: { customRender: 'name' } },
  { align: 'center', title: '当日收盘价', dataIndex: 'closeingPrice' },
  { align: 'center', title: '当日涨跌幅(%)', dataIndex: 'quoteChange' },
  { align: 'center', title: '持股数量', dataIndex: 'holdingNum', sorter: true },
  { align: 'center', title: '持股市值', dataIndex: 'holdingValue', sorter: true },
  { align: 'center', title: '持股数量占A股百分比(%)', dataIndex: 'holdingNumPercent', sorter: true }
]
export default {
  name: 'shHold',
  data() {
    return {
      dateActive: '',
      dateFormat: 'YYYY-MM-DD',
      tableData: [],
      startTime: '', // 开始时间 YYYY-MM-DD
      endTime: '', // 结束时间 YYYY-MM-DD
      searchData: [], // 输入框模糊搜索列表 格式 { value: 1, text: 1 }
      content: undefined, // 输入框内容
      holdingNum: undefined, // 持股数量 1-升序，2-降序（一次只能传一个)
      holdingValue: undefined, // 持股数值
      holdingNumPercent: undefined, // 持股百分比
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.startTime = moment().day(-365).format('YYYY-MM-DD')
    this.endTime = moment().format('YYYY-MM-DD')
    this.getData()
  },
  methods: {
    moment,
    handleClickName(record) {
      this.$router.push('/stockInfo?code=' + record.code)
    },
    handleTableChange(pagination, filters, sorter) {
      this.holdingNum = (sorter.columnKey === 'holdingNum' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.holdingValue = (sorter.columnKey === 'holdingValue' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.holdingNumPercent = (sorter.columnKey === 'holdingNumPercent' && (sorter.order === 'ascend' ? 1 : 2)) || undefined
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      let data = {
        content: this.content,
        startTime: this.startTime,
        endTime: this.endTime,
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize,
        holdingNum: this.holdingNum,
        holdingValue: this.holdingValue,
        holdingNumPercent: this.holdingNumPercent
      }
      this.loading = true
      getShHoldingShares(data).then(res => {
        console.log('沪股通持股')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    },
    // 起止日期选框
    onDateChange(val, dateArr) {
      this.dateActive = '' // 清空选中状态
      // dateArr 格式为["2019-05-07", "2019-05-11"]
      this.startTime = dateArr[0]
      this.endTime = dateArr[1]
      this.getData()
    },
    handleChange(value) {
      console.log(value)
      this.content = value
      this.getData()
    },
    // 输入即时搜索
    handleSearch(val) {
      let data = {
        pageNum: 1,
        pageSize: 10,
        content: val
      }
      getShHoldingShareList(data).then(res => {
        console.log(res)
        if (res.code === 200) {
          let arr = []
          let obj = res.data.data
          for (let i = 0; i < obj.length; i++) {
            arr.push({ value: obj[i].code, text: obj[i].code + ' ' + obj[i].name })
          }
          this.searchData = arr
        }
      })
    },
    // 切换当日、三日、五日、十日
    handleDateActive(args) {
      this.dateActive = args
      switch (args) {
        case 1:
          console.log(args)
          this.startTime = moment().format('YYYY-MM-DD')
          break
        case 2:
          this.startTime = moment().day(-2).format('YYYY-MM-DD')
          break
        case 3:
          this.startTime = moment().day(-4).format('YYYY-MM-DD')
          break
        case 4:
          this.startTime = moment().day(-9).format('YYYY-MM-DD')
          break
        default:
          break
      }
      this.endTime = moment().format('YYYY-MM-DD')
      this.getData()
    }
  }
}
</script>
<style lang="scss" scoped>
.search-wrap {
  padding: 10px 20px 14px;
  // text-align: right;
  .menu-date {
    float: right;
    .menu-li {
      display: inline-block;
      // margin: 0 4px;
      // width: 56px;
      padding: 0 15px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      cursor: pointer;
      font-size: 14px;
    }
    .menu-li.active {
      background-color: #2286FF;
      color: #fff;
    }
  }
}
</style>
